.results-container {
  background: #535050;
  width: 380px;
  margin-top: 5px;
  position: absolute;
  z-index: 5;
  color: black;
}

.showLink {
  text-decoration: none;
}

.show-container {
  display: flex;
  height: 100px;
  padding-right: 10px;
}

.show-container:hover {
  background: #292727c2;
  border: 1px solid orange;
  cursor: pointer;
}

.results-show-img {
  width: 73px;
}

.restuls-show-text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: 0.9rem;
  padding-left: 12px;
  color: white;
}

.results-show-title {
  font-size: 1rem;
  font-weight: bold;
  padding-bottom: 5px;
  color: orange;
}
