.nav-bar {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 15px 0;
  z-index: 1;
}

.logo {
  text-decoration: none;
  color: white;
}

.nav-left {
  display: flex;
  align-items: center;
}

.logo {
  font-size: 1.4rem;
  font-weight: bold;
}

.nav-left .link {
  text-decoration: none;
  color: rgba(255, 255, 255, 0.712);
  padding-left: 30px;
}

.link:hover {
  color: orange;
}
